import React, { Component } from "react";

class Grid extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      numberMap: this.props.numberMap,
      health: this.props.health,
      experience: this.props.experience,
      damage: this.props.damage,
      items: this.props.items,
      level: this.props.level,
      room: this.props.room,
      dead: this.props.dead,
      maxHealth: this.props.maxHealth,
      maxExperience: this.props.maxExperience,
      refresh: this.props.refresh,
      won: this.props.won
    };
  }

  getPlayerPosition() {
    let playerPosition;
    let numberMap = this.state.numberMap;
    for (let i = 0; i < numberMap.length; i++) {
      for (let y = 0; y < numberMap[i].length; y++) {
        if (numberMap[i][y] === 2) {
          playerPosition = [i, y];
        }
      }
    }
    return playerPosition;
  }
  
  getMap() {
    let objectMap = [];
    let numberMap = this.state.numberMap;
    let playerPosition = this.getPlayerPosition();

    for (let i = 0; i < numberMap.length; i++) {
      for (let y = 0; y < numberMap[i].length; y++) {
        let vertical = playerPosition[0] - i;
        let horizontal = playerPosition[1] - y;

        if (
          horizontal < 4 &&
          horizontal > -4 &&
          vertical < 4 &&
          vertical > -4
        ) {
          if (numberMap[i][y] === 0) {
            objectMap.push(<div className="terrain" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 1) {
            objectMap.push(<div className="wall" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 2) {
            objectMap.push(<div className="player" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 3) {
            objectMap.push(
              <div
                className="cop"
                key={i + "_" + y}
                health={100}
                damage={20 * (0.25 * this.state.room)}
              />
            );
          }
          if (numberMap[i][y] === 4) {
            objectMap.push(<div className="weapon" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 5) {
            objectMap.push(<div className="medicine" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 6) {
            objectMap.push(<div className="boss" key={i + "_" + y} health={300} damage={20}/>);
          }
          if (numberMap[i][y] === 9) {
            objectMap.push(<div className="door" key={i + "_" + y} />);
          }
          if (numberMap[i][y] === 99) {
            objectMap.push(<div className="fogged" key={i + "_" + y} />);
          }
        } else {
          objectMap.push(<div className="fogged" key={i + "_" + y} />);
        }
      }
    }
    this.setState({ objectMap: objectMap });
  }
  setPosition() {
    this.setPositionOfObjects(1, 2);
    this.setPositionOfObjects(10, 3);
    this.setPositionOfObjects(3, 4);
    this.setPositionOfObjects(3, 5);
    if (this.state.room < 3) {
    this.setPositionOfObjects(1, 9);}
    if (this.state.room === 3) {
      this.setPositionOfObjects(1,6);
    }
  }
  setPositionOfObjects(howMany, index) {
    let numberMap_empty = this.state.numberMap;
    for (let what = 0; what < howMany; ) {
      let randomRow = Math.round(Math.random() * 31);
      let randomCol = Math.round(Math.random() * 31);
      if (numberMap_empty[randomRow][randomCol] === 0) {
        numberMap_empty[randomRow][randomCol] = index;
        what++;
      }
    }
    this.setState({ numberMap: numberMap_empty });
  }
  addingBorder() {
    let toAddBorders = this.state.numberMap;
    for (let i = 0; i < toAddBorders.length; i++) {
      toAddBorders[i].unshift(99);
      toAddBorders[i].unshift(99);
      toAddBorders[i].push(99);
      toAddBorders[i].push(99);
    }

    let bottomAndTopBorders = [[], []];
    for (let i = 0; i < toAddBorders[0].length; i++) {
      bottomAndTopBorders[0].push(99);
      bottomAndTopBorders[1].push(99);
    }
    toAddBorders.push(bottomAndTopBorders[0], bottomAndTopBorders[1]);
    toAddBorders.unshift(bottomAndTopBorders[0], bottomAndTopBorders[1]);
    this.setState({ numberMap: toAddBorders });
  }
  generateRandomItem() {
    let itemName = [
      "Pillow",
      "Pencil",     
      "Pen",
      "Shoelace",
      "Almost empty can of pepper spray",
      "Small piece of metal",
      "Low-grade toothbrush shank",
      "Makeshift brass knuckles",
      "Shank made of old glasses",
      "Nightstick",
      "Yellow pages armour",
      "Tazer"
    ];
    let itemDamage = [1, 1, 1, 2, 2, 3, 3, 4, 5, 5, 6, 6];
    let howManyItems = this.state.items.length;
    let currentItems = this.state.items;
    currentItems.push(itemName[howManyItems]);
    this.setState({items:currentItems, damage:this.state.damage+itemDamage[howManyItems]});
    this.props.handlerItems(currentItems);
    this.props.handlerDamage(this.state.damage+itemDamage[howManyItems]);
  }

  playerDoesDamage(whatCop) {
    let x = whatCop[0];
    let y = whatCop[1];
    let index = x * 35 + x + y;
    if (x === 0) {
      index--;
    }
    let damage = Math.round(Math.random() * 3);
    damage = damage + this.state.damage + (damage + this.state.level) * 0.25;
    let objectMap = this.state.objectMap;
    let copHP = objectMap[index].props.health;
    let numberMap = this.state.numberMap;

    if (this.state.numberMap[x][y]=== 3) {
    objectMap[index] = (
      <div
        className="cop"
        key={x + "_" + y}
        health={copHP - damage}
        damage={20 * (0.25 * this.state.room)}
      />
    );
    }
    if (this.state.numberMap[x][y]=== 6) {
    objectMap[index] = (
      <div
        className="boss"
        key={x + "_" + y}
        health={copHP - damage}
        damage={20}
      />
    );
    }

    if (objectMap[index].props.health < 0 && numberMap[x][y]===3) {
      objectMap[index] = <div className="terrain" key={x + "_" + y} />;
      let numberMap = this.state.numberMap;
      numberMap[x][y] = 0;
      let experience = this.state.experience + 100;
      this.props.handlerExperience(experience);
      this.setState({ numberMap: numberMap, experience: experience });
      if (this.state.experience >= this.state.maxExperience) {
        let newLevel = this.state.level + 1;
        this.props.handlerDamage(this.state.damage + 10);
        this.props.handlerLevel(newLevel);
        this.props.handlerExperience(0);
        this.setState({
          level: newLevel,
          experience: 0,
          damage: this.state.damage + 10
        });
      }
    }
    else if (objectMap[index].props.health < 0 && numberMap[x][y]===6) {
      objectMap[index] = <div className="terrain" key={x + "_" + y} />;
      let numberMap = this.state.numberMap;
      numberMap[x][y] = 0;
      this.setState({ numberMap: numberMap});
      this.setState({ won: "yes" });
      this.props.handlerWon("yes");
    } 
     else {
      this.copDoesDamage(whatCop);
    }


    this.setState({ objectMap: objectMap });
  }
  copDoesDamage(whatCop) {
    let x = whatCop[0];
    let y = whatCop[1];
    let index = x * 35 + x + y;
    if (x === 0) {
      index--;
    }
    let bonusDamage = Math.round(Math.random() * 5);
    let damage = this.state.objectMap[index].props.damage + bonusDamage;
    let myHealth = this.state.health;
    myHealth = myHealth - damage;
    this.props.handlerHealth(myHealth);
    this.setState({ health: myHealth });
    if (this.state.health <= 0) {
      this.setState({ dead: "yes" });
      this.props.handlerDead("yes");
    }
  }

  /* GENERATE ARRAY OF 1s WITH RANDOM WALK */
  createArray(num, dimensions) {
    var array = [];
    for (var i = 0; i < dimensions; i++) {
      array.push([]);
      for (var j = 0; j < dimensions; j++) {
        array[i].push(num);
      }
    }
    return array;
  }
  createMap() {
    let dimensions = 32,
      maxTunnels = 100,
      maxLength = 20,
      map = this.createArray(1, dimensions),
      currentRow = Math.floor(Math.random() * dimensions),
      currentColumn = Math.floor(Math.random() * dimensions),
      directions = [[-1, 0], [1, 0], [0, -1], [0, 1]],
      lastDirection = [],
      randomDirection;

    while (maxTunnels && dimensions && maxLength) {
      do {
        randomDirection =
          directions[Math.floor(Math.random() * directions.length)];
      } while (
        (randomDirection[0] === -lastDirection[0] &&
          randomDirection[1] === -lastDirection[1]) ||
        (randomDirection[0] === lastDirection[0] &&
          randomDirection[1] === lastDirection[1])
      );

      var randomLength = Math.ceil(Math.random() * maxLength),
        tunnelLength = 0;

      while (tunnelLength < randomLength) {
        if (
          (currentRow === 0 && randomDirection[0] === -1) ||
          (currentColumn === 0 && randomDirection[1] === -1) ||
          (currentRow === dimensions - 1 && randomDirection[0] === 1) ||
          (currentColumn === dimensions - 1 && randomDirection[1] === 1)
        ) {
          break;
        } else {
          map[currentRow][currentColumn] = 0;
          currentRow += randomDirection[0];
          currentColumn += randomDirection[1];
          tunnelLength++;
        }
      }

      if (tunnelLength) {
        lastDirection = randomDirection;
        maxTunnels--;
      }
    }
    return map;
  }

  nextRoom() {
    let numberMap = this.props.backupMap;
    this.setState({ numberMap: numberMap });
    this.setPosition();
    this.addingBorder();
    this.getMap();
    this.props.handlerRoom(this.state.room + 1);
    this.setState({ room: this.state.room + 1 });
  }

  handleKeyDown = event => {
    let playerPosition = this.getPlayerPosition();
    let numberMap = this.state.numberMap;

    if (
      numberMap[playerPosition[0]][playerPosition[1] - 1] !== 3 &&
      numberMap[playerPosition[0] - 1][playerPosition[1]] !== 3 &&
      numberMap[playerPosition[0]][playerPosition[1] + 1] !== 3 &&
      numberMap[playerPosition[0] + 1][playerPosition[1]] !== 3 &&
      numberMap[playerPosition[0]][playerPosition[1] - 1] !== 6 &&
      numberMap[playerPosition[0] - 1][playerPosition[1]] !== 6 &&
      numberMap[playerPosition[0]][playerPosition[1] + 1] !== 6 &&
      numberMap[playerPosition[0] + 1][playerPosition[1]] !== 6
    ) {
      if (event.keyCode === 37) {
        if (numberMap[playerPosition[0]][playerPosition[1] - 1] === 0) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] - 1] = 2;
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] - 1] === 4) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] - 1] = 2;
          this.generateRandomItem();
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] - 1] === 5) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] - 1] = 2;
          let newHealth = this.state.health + 20;
          if (newHealth > this.state.maxHealth) {
            newHealth = this.state.maxHealth;
          }
          this.setState({ numberMap: numberMap, health: newHealth });
          this.props.handlerHealth(newHealth);
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] - 1] === 9) {
          this.nextRoom();
        }
      }
      if (event.keyCode === 38) {
        if (numberMap[playerPosition[0] - 1][playerPosition[1]] === 0) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] - 1][playerPosition[1]] = 2;
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0] - 1][playerPosition[1]] === 4) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] - 1][playerPosition[1]] = 2;
          this.generateRandomItem();
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0] - 1][playerPosition[1]] === 5) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] - 1][playerPosition[1]] = 2;
          let newHealth = this.state.health + 20;
          if (newHealth > this.state.maxHealth) {
            newHealth = this.state.maxHealth;
          }
          this.setState({ numberMap: numberMap, health: newHealth });
          this.props.handlerHealth(newHealth);
          this.getMap();
        }
        if (numberMap[playerPosition[0] - 1][playerPosition[1]] === 9) {
          this.nextRoom();
        }
      }
      if (event.keyCode === 39) {
        if (numberMap[playerPosition[0]][playerPosition[1] + 1] === 0) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] + 1] = 2;
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] + 1] === 4) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] + 1] = 2;
          this.generateRandomItem();
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] + 1] === 5) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0]][playerPosition[1] + 1] = 2;
          let newHealth = this.state.health + 20;
          if (newHealth > this.state.maxHealth) {
            newHealth = this.state.maxHealth;
          }
          this.setState({ numberMap: numberMap, health: newHealth });
          this.props.handlerHealth(newHealth);
          this.getMap();
        }
        if (numberMap[playerPosition[0]][playerPosition[1] + 1] === 9) {
          this.nextRoom();
        }
      }
      if (event.keyCode === 40) {
        if (numberMap[playerPosition[0] + 1][playerPosition[1]] === 0) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] + 1][playerPosition[1]] = 2;
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0] + 1][playerPosition[1]] === 4) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] + 1][playerPosition[1]] = 2;
          this.generateRandomItem();
          this.setState({ numberMap: numberMap });
          this.getMap();
        }
        if (numberMap[playerPosition[0] + 1][playerPosition[1]] === 5) {
          numberMap[playerPosition[0]][playerPosition[1]] = 0;
          numberMap[playerPosition[0] + 1][playerPosition[1]] = 2;
          let newHealth = this.state.health + 20;
          if (newHealth > this.state.maxHealth) {
            newHealth = this.state.maxHealth;
          }
          this.setState({ numberMap: numberMap, health: newHealth });
          this.props.handlerHealth(newHealth);
          this.getMap();
        }
        if (numberMap[playerPosition[0] + 1][playerPosition[1]] === 9) {
          this.nextRoom();
        }
      }
    } else {
      if (numberMap[playerPosition[0]][playerPosition[1] - 1] === 3 ||numberMap[playerPosition[0]][playerPosition[1] - 1] === 6) {
        let whatCop = [playerPosition[0], playerPosition[1] - 1];
        this.playerDoesDamage(whatCop);
      }
      if (numberMap[playerPosition[0] - 1][playerPosition[1]] === 3 ||numberMap[playerPosition[0] - 1][playerPosition[1]] === 6) {
        let whatCop = [playerPosition[0] - 1, playerPosition[1]];
        this.playerDoesDamage(whatCop);
      }
      if (numberMap[playerPosition[0]][playerPosition[1] + 1] === 3 ||numberMap[playerPosition[0]][playerPosition[1] + 1] === 6) {
        let whatCop = [playerPosition[0], playerPosition[1] + 1];
        this.playerDoesDamage(whatCop);
      }
      if (numberMap[playerPosition[0] + 1][playerPosition[1]] === 3 ||numberMap[playerPosition[0] + 1][playerPosition[1]] === 6) {
        let whatCop = [playerPosition[0] + 1, playerPosition[1]];
        this.playerDoesDamage(whatCop);
      }
    }

    let newPlayerPosition = this.getPlayerPosition();
    if (
      numberMap[newPlayerPosition[0]][newPlayerPosition[1] - 1] === 3 ||
      numberMap[newPlayerPosition[0] - 1][newPlayerPosition[1]] === 3 ||
      numberMap[newPlayerPosition[0]][newPlayerPosition[1] + 1] === 3 ||
      numberMap[newPlayerPosition[0] + 1][newPlayerPosition[1]] === 3 ||
      numberMap[newPlayerPosition[0]][newPlayerPosition[1] - 1] === 6 ||
      numberMap[newPlayerPosition[0] - 1][newPlayerPosition[1]] === 6 ||
      numberMap[newPlayerPosition[0]][newPlayerPosition[1] + 1] === 6 ||
      numberMap[newPlayerPosition[0] + 1][newPlayerPosition[1]] === 6 
    ) {

    }
  };

  componentDidMount() {
    this.setPosition();
    this.addingBorder();
    this.getMap();
  }
  componentWillMount() {
    document.addEventListener("keydown", this.handleKeyDown, false);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyDown, false);
  }

  render() {
    return <div className="gameGrid">{this.state.objectMap}</div>;
  }
}

export default Grid;


