import React, { Component } from "react";
import "./App.css";
import Panel from "react-bootstrap/lib/Panel";
import Grid from "./Grid";
import ProgressBar from "react-bootstrap/lib/ProgressBar";
import Button from "react-bootstrap/lib/Button";

class App extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      open: false,
      health: 100,
      experience: 0,
      items: [],
      damage: 20,
      level: 1,
      maxHealth: 100,
      room: 1,
      dead: "no",
      maxExperience: 500,
      won: "no"
    };

    this.handlerHealth = this.handlerHealth.bind(this);
    this.handlerExperience = this.handlerExperience.bind(this);
    this.handlerItems = this.handlerItems.bind(this);
    this.handlerDamage = this.handlerDamage.bind(this);
    this.handlerDead = this.handlerDead.bind(this);
    this.handlerLevel = this.handlerLevel.bind(this);
    this.handlerRoom = this.handlerRoom.bind(this);
    this.handlerWon = this.handlerWon.bind(this);
  }

  /*Binding THIS to update state from Grid*/
  handlerWon(x) {
    this.setState({
      won: x
    });
  }
  handlerHealth(x) {
    this.setState({
      health: x
    });
  }

  handlerExperience(x) {
    this.setState({
      experience: x
    });
  }

  handlerRoom(x) {
    this.setState({
      room: x
    });
  }

  handlerMaxHealth(x) {
    this.setState({
      maxHealth: x
    });
  }

  handlerItems(x) {
    this.setState({
      items: x
    });
  }

  handlerDamage(x) {
    this.setState({
      damage: x
    });
  }
  handlerDead(x) {
    this.setState({
      dead: x
    });
  }
  handlerLevel(x) {
    this.setState({
      level: x
    });
  }

  refreshPage() {
    window.location.reload();
  }

  /* GENERATE ARRAY OF 1s WITH RANDOM WALK */
  createArray(num, dimensions) {
    var array = [];
    for (var i = 0; i < dimensions; i++) {
      array.push([]);
      for (var j = 0; j < dimensions; j++) {
        array[i].push(num);
      }
    }
    return array;
  }

  createMap() {
    let dimensions = 32,
      maxTunnels = 100,
      maxLength = 20,
      map = this.createArray(1, dimensions),
      currentRow = Math.floor(Math.random() * dimensions),
      currentColumn = Math.floor(Math.random() * dimensions),
      directions = [[-1, 0], [1, 0], [0, -1], [0, 1]],
      lastDirection = [],
      randomDirection;

    while (maxTunnels && dimensions && maxLength) {
      do {
        randomDirection =
          directions[Math.floor(Math.random() * directions.length)];
      } while (
        (randomDirection[0] === -lastDirection[0] &&
          randomDirection[1] === -lastDirection[1]) ||
        (randomDirection[0] === lastDirection[0] &&
          randomDirection[1] === lastDirection[1])
      );

      var randomLength = Math.ceil(Math.random() * maxLength),
        tunnelLength = 0;

      while (tunnelLength < randomLength) {
        if (
          (currentRow === 0 && randomDirection[0] === -1) ||
          (currentColumn === 0 && randomDirection[1] === -1) ||
          (currentRow === dimensions - 1 && randomDirection[0] === 1) ||
          (currentColumn === dimensions - 1 && randomDirection[1] === 1)
        ) {
          break;
        } else {
          map[currentRow][currentColumn] = 0;
          currentRow += randomDirection[0];
          currentColumn += randomDirection[1];
          tunnelLength++;
        }
      }

      if (tunnelLength) {
        lastDirection = randomDirection;
        maxTunnels--;
      }
    }
    return map;
  }

  render() {
    let itemsDisplayed = [];
    for (let i = 0; i < this.state.items.length; i++) {
      itemsDisplayed.push(
        <div key={"ITEM" + i}>
          {this.state.items[i]} <br />
        </div>
      );
    }

    return (
      <div>
        <div className="mobileScreenDisplay">
          This game is best played in a larger screen.
        </div>
        <div className="App container-fluid">
          <div className="row">
            <div className="nav col-xs-2">
              <br />
              <h1><b>Room {this.state.room}</b></h1>
              <div className="progressBarHolder">
                <h4><b>Health Bar</b></h4>

                <div className="progress">
                  <ProgressBar
                    bsStyle="danger"
                    now={this.state.health}
                    label={`${this.state.health} HP`}
                  />;
                </div>
                <br />
              </div>

              <div className="experienceBarHolder">
                <h4><b>Experience Bar</b></h4>
                <h5>Level: {this.state.level}</h5>
                <div className="progress">
                  <ProgressBar
                    bsStyle="warning"
                    now={this.state.experience}
                    label={`${this.state.experience} xp`}
                    max={this.state.maxExperience}
                  />;
                </div>
              </div>
              <br />
              <div className="damageHolder">
                <h4><b>Damage</b></h4>
                {this.state.damage}-{this.state.damage + 3}
              </div>
              <br />
              <div className="itemsHolder">
                <h4><b>Items</b></h4>
                {itemsDisplayed}
              </div>
              <br />
            </div>
            <div
              className="header col-xs-10"
              onClick={() =>
                this.setState({
                  open: !this.state.open
                })
              }
            >
              <h1><b>Prison Escape</b></h1>
              <Panel
                expanded={this.state.open}
                className="collapsablePanel"
                onToggle=".header"
              >
                <Panel.Collapse>
                  <Panel.Body className="insidePanel">
                  <div>
                    <div className="copyright">coded by Rafael Pessoa</div>
                    <h2 style={{color:"lightgrey"}}>Instructions</h2>
                    <p>Prison Escape is a simple game developed for the Data Visualization Certification from FreeCodeCamp, using ReactJS.
                    The game is as simple graphically speaking as it is gameplay wise. You control the main character, a prisioner trying to escape a randomly generated prison, with just your arrow keys on the keyboard:</p>
                    <div className="arrowkeys" /><br/>
                    <ul>
                    <li><div className="cop" />&nbsp; This is a correctional officer. Once your current position is surrended by at least one correctional officer, you won't be able to move and will be forced to attack. To attack, just simply walk to the square the correctional officer is at.</li>
                    <li><div className="weapon" />&nbsp; This is a random item. It increases your damage, there are 12 of them spread across the 4 prison rooms.</li>
                    <li><div className="medicine" />&nbsp; This is medicine. It heals 20 health.</li>
                    <li><div className="door" />&nbsp; This is a door to the next room. Once a room is left, the door gets locked and you can't enter it again.</li>
                    <li><div className="boss" />&nbsp; This is the final boss that holds the key. Once you kill the boss, you win the game.</li>
                    </ul>
                  </div>
                  </Panel.Body>
                </Panel.Collapse>
              </Panel>
              <div>
                <div
                  className={!this.state.open ? "showDownArrow" : "hideArrow"}
                >
                  <i className="fas fa-sort-down" />
                </div>
                <div className={!this.state.open ? "hideArrow" : "showUpArrow"}>
                  <i className="fas fa-sort-up" />
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-2" />
            <div className="col-xs-10">
              <div
                className="deathScreen"
                style={{ display: this.state.dead === "no" ? "none" : "block" }}
              >
                You lost.
                <div className="restartButtonHolder">
                  <Button onClick={this.refreshPage}>Restart</Button>
                </div>
              </div>
              <div
                className="deathScreen"
                style={{ display: this.state.won === "no" ? "none" : "block" }}
              >
                You won the game! Press to play again.
                <div className="restartButtonHolder">
                  <Button onClick={this.refreshPage}>Restart</Button>
                </div>
              </div>
              <Grid
                key="grid"
                className="gameGrid"
                numberMap={this.createMap()}
                backupMap={this.createMap()}
                handlerHealth={this.handlerHealth}
                handlerDamage={this.handlerDamage}
                handlerItems={this.handlerItems}
                handlerExperience={this.handlerExperience}
                handlerDead={this.handlerDead}
                health={this.state.health}
                experience={this.state.experience}
                damage={this.state.damage}
                items={this.state.items}
                room={this.state.room}
                level={this.state.level}
                dead={this.state.dead}
                maxHealth={this.state.maxHealth}
                handlerMaxHealth={this.handlerMaxHealth}
                maxExperience={this.state.maxExperience}
                handlerLevel={this.handlerLevel}
                handlerRoom={this.handlerRoom}
                handlerWon={this.handlerWon}
                refresh={this.refreshPage}
                won= {this.state.won}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;


